import { Component, Input } from '@angular/core';
import { Node } from '../../../d3';
import {MatBottomSheet, MatBottomSheetRef} from '@angular/material';

@Component({
  selector: '[nodeVisual]',
  template: `
  <img src="https://s3-us-west-2.amazonaws.com/s.cdpn.io/44216/pixels-2.svg" alt="Pixels, my super cute cat">
  <svg:g [attr.transform]="'translate(' + node.x + ',' + node.y + ')'">
      <svg:circle
          (click)="openBottomSheet(node.id)"
          class="node"
          [attr.fill]="node.bgcolor"
          cx="0"
          cy="0"
          [attr.r]="node.r + 5">
      </svg:circle>
      <svg:text
          class="node-name"
          [attr.font-size]="node.fontSize">
        {{node.id}}.{{node.title}}
      </svg:text>
      </svg:g>
      <svg:g [attr.transform]="'translate(' + 200 + ',' + 200 + ')'">
      <svg:circle
          (click)="openBottomSheet()"
          class="node"
          [attr.fill]="node.bgcolor"
          cx="0"
          cy="0"
          [attr.r]="node.r">
      </svg:circle>
      <svg:text
          class="node-name"
          [attr.font-size]="node.fontSize">
        {{node.desc}}
      </svg:text>
      <g id="play-activity">
					<rect x="752.3" y="277.3" class="st17" width="201.3" height="680"/>
					<text id="play-description" transform="matrix(1 0 0 1 260.9023 490.2235)">
						<tspan x="0" y="0" class="st18 st14 st19">MAKES YOUR CITY</tspan>
						<tspan x="21" y="21.6" class="st18 st14 st19">INTO SMART</tspan><tspan x="61.5" y="43.2" class="st18 st14 st19">CITY.</tspan>
						<tspan class="offscreen">A gray kitten tangled in a ball of yarn.</tspan>
					</text>
					<image style="overflow:visible;enable-background:new;" width="259" height="259" xlink:href="https://s3-us-west-2.amazonaws.com/s.cdpn.io/44216/cat_tied.png" transform="matrix(0.3707 0 0 0.3707 304.9196 361.7916)">
					</image>
        </g>
      </svg:g>
  `,
  styleUrls: ['./node-visual.component.css']
})
export class NodeVisualComponent {
  @Input('nodeVisual') node: Node;
  public showDiv = false;
  public showDivStyle = 'visibility: hidden';
  testText = 'Test text on hover';

  constructor(private bottomSheet: MatBottomSheet) {}

  openBottomSheet(): void {
    this.bottomSheet.open(BottomSheetOverviewExampleSheet);
  }
}


@Component({
  selector: 'bottom-sheet-overview-example-sheet',
  template: `
  <mat-nav-list>
  <a href="https://keep.google.com/" mat-list-item (click)="openLink($event)">
    <span mat-line>Google Keep</span>
    <span mat-line>Add to a note</span>
  </a>

  <a href="https://docs.google.com/" mat-list-item (click)="openLink($event)">
    <span mat-line>Google Docs</span>
    <span mat-line>Embed in a document</span>
  </a>

  <a href="https://plus.google.com/" mat-list-item (click)="openLink($event)">
    <span mat-line>Google Plus</span>
    <span mat-line>Share with your friends</span>
  </a>

  <a href="https://hangouts.google.com/" mat-list-item (click)="openLink($event)">
    <span mat-line>Google Hangouts</span>
    <span mat-line>Show to your coworkers</span>
  </a>
</mat-nav-list>
  `,
})
export class BottomSheetOverviewExampleSheet {
  constructor(private bottomSheetRef: MatBottomSheetRef<BottomSheetOverviewExampleSheet>) {}

  openLink(event: MouseEvent): void {
    this.bottomSheetRef.dismiss();
    event.preventDefault();
  }
}
